			http.httpTokenRequest(opts, param).then(res => {
				console.log(res.data);
				uni.hideLoading()
			}, error => {
				console.log(error);
				uni.hideLoading()
			})
		},
		showToast: function(title, type, url, param) {
			this.$refs.uToast.show({
				title: title,
				type: type,
				url: url,
				param: param
			})
		},
		goPage: function(e) {
			if (e == 'null') {
				this.showToast('开发中', 'info')
			} else {
